import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class StorageService {
  project: any = {};
  nextProject: String;
  constructor() {
  }

  setProject(value){
    this.project = value
  }
  setNextProject(value){
    this.nextProject = value
  }
  getProject(){
    if(this.project.id != undefined)
      return this.project.id
    else
      return "proy-7"
  }
  getNextProject(){
    return this.nextProject
  }
}
