import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { environment } from "../../environments/environment";
import { StorageService } from './storage.service';
@Injectable({
  providedIn: 'root'
})
export class PersonalInfoService {
  
  data: any = {}
  personal: any = {}
  projects: any = []
  loading: boolean
  loadingProjects: boolean = true;
  url: string;
  constructor(public httpClient:HttpClient, public storageService:StorageService) { 
    this.url = environment.production ? 'https://myportafolio-6997b.firebaseio.com' : 'assets/data'; 
    this.getData(this.url);
    
  }

  getData(url){
   
      this.httpClient.get(`${url}/personal.json`)
      .subscribe( data=>{
        this.data = data
        this.personal = this.data 
        this.loading = false;
      })

      this.httpClient.get(`${url}/projects.json`)
      .subscribe( data=>{
        this.data = data 
        this.projects = this.data
        this.loadingProjects = false
      })
      
  }

  getProject(){
    if(environment.production)
      return this.httpClient.get(`${this.url}/project/${this.storageService.getProject()}.json`) 
    else
      return this.httpClient.get(`${this.url}/project.json`)
  }
}
